@charset "utf-8";
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&display=swap');

html {
  box-sizing: border-box;
  font-family: 'Lato', sans-serif;
  font-size: 100%;
}

body {
  line-height: 1;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

img {
  border: 0;
  display: block;
  height: auto;
  max-width: 100%;
}
